import {Injectable} from '@angular/core';
import {Http} from '@angular/http';

export class Book {

  constructor(public id: number, public title: string, public author: string, public description: string, public cover: string) {}

}

let BOOKS = [
  new Book(11,
    'Harry Potter',
    'Rowling',
    'Harry remegő kézzel megfordította a küldeményt.',
    'http://books.google.com/books/content?id=bF19DAAAQBAJ&printsec=frontcover&img=1&zoom=1&edge=curl&source=gbs_api'),
  new Book(12, 'Harry Potter 2', 'Rowling', 'Harry Potter books',
    'http://books.google.com/books/content?id=YvQ_AhkJpBUC&printsec=frontcover&img=1&zoom=5&edge=curl&source=gbs_api')
];


@Injectable()
export class BookService {

  private search_url = 'https://www.googleapis.com/books/v1/volumes';

  constructor(private http: Http) { }

  bookPromise = Promise.resolve(BOOKS);

  getBooks() {
    return this.bookPromise;
  }

  getBook(id) {
    return this.bookPromise
    // (+) before `id` turns the string into a number
      .then(books => books.find(book => book.id === +id));
  }

}



